import { useEffect, useState } from "react";
import { fetchAllOrdersAsync, updateOrderAsync } from "../../order/orderSlice";
import { ITEMS_PER_PAGE, discountedPrice } from "../../../app/constants";
import { useDispatch, useSelector } from "react-redux";
import { PencilIcon, EyeIcon, ArrowUpIcon,ArrowDownIcon} from "@heroicons/react/24/outline";
import { Pagination } from "../../common/Pagination";

const AdminOrders = () => {
  const [page, setPage] = useState(1);
  const orders = useSelector((state) => state.order.orders);
  const totalOrders = useSelector((state) => state.order.totalOrders);
  const dispatch = useDispatch();
  const [sort, setSort] = useState({});
  const [editableOrderId, setEditableOrderId] = useState(-1);
  const handleShow = (order) => {};
  const handleEdit = (order) => {
    setEditableOrderId(order.id);
  };

  const handleUpdate = (e, order) => {
    const updatedOrder = { ...order, status: e.target.value };
    console.log(e.target.value);
    dispatch(updateOrderAsync(updatedOrder));
    // as soon as like you will change pending to delivered so it will show delivered in the style format not in the select style
    setEditableOrderId(-1);
  };

  const handlePage = (page) => {
    setPage(page);
  };
  const handleSort = (sortOption) => {
    // here in the json server it support sort querey like http://localhost:8080/products?_sort:price&_order:asc
    // means sort the price in the ascending order that is why both should be given and always remember that map could have only unique keys means if again you select any other sorting so same _sort and _order field would be replaced and same in case of filters
    const newSort = { _sort: sortOption.sort, _order: sortOption.order };
    setSort(newSort);
  };
  const chooseColor = (status) => {
    switch (status) {
      case "pending":
        return "bg-purple-200 text-purple-600";
      case "dispatched":
        return "bg-yellow-200 text-yellow-600";
      case "delivered":
        return "bg-green-200 text-green-600";
      case "cancelled":
        return "bg-red-200 text-red-600";
      default:
        return "bg-purple-200 text-purple-600";
    }
  };
  useEffect(() => {
    const pagination = { _page: page, _limit: ITEMS_PER_PAGE };
    // In Redux Toolkit's createAsyncThunk function, you cannot directly pass multiple parameters to the thunk function without using an object as the sole argument.
    dispatch(fetchAllOrdersAsync({ sort, pagination }));
    // here as soon as dispatch and filter would have some changes useEffect will be called itself
  }, [dispatch, page, sort]);

  return (
    <>
      <div className="overflow-x-auto  ">
        <div className="flex items-center justify-center  bg-gray-100 font-sans overflow-hidden">
          <div className="w-full">
            <div className="bg-white  shadow-md rounded my-6 ">
              <table className=" table-auto w-full">
                <thead>
                  <tr className="bg-gray-200 text-gray-600 uppercase text-sm leading-normal">
                    <th
                      className="cursor-pointer  py-3 px-6 text-left"
                      onClick={(e) =>
                        handleSort({
                          sort: "id",
                          order: sort._order === "asc" ? "desc" : "asc",
                        })
                      }
                    >
                      Order#{" "}
                      {sort._sort === 'id'&& (sort._order === 'asc'? (
                        <ArrowUpIcon className="w-4 h-4 inline"></ArrowUpIcon>):(<ArrowDownIcon className="w-4 h-4 inline "></ArrowDownIcon>)
                      )}
                    </th>
                    <th className="py-3 px-6 text-left">Items</th>
                    {/* below we have done a sorting like in ascending order or desc order of totalamount  */}
                    <th
                      className="cursor-pointer  py-3 px-6 text-center"
                      onClick={(e) =>
                        handleSort({
                          sort: "totalAmount",
                          order: sort._order === "asc" ? "desc" : "asc",
                        })
                      }
                    >
                      Total Amount{" "}
                      {sort._sort === 'totalAmount'&& (sort._order === 'asc'? (
                        <ArrowUpIcon className="w-4 h-4 inline"></ArrowUpIcon>):(<ArrowDownIcon className="w-4 h-4 inline "></ArrowDownIcon>)
                      )}
                    </th>
                    <th className="py-3 px-6 text-center">Shipping Address</th>
                    <th className="py-3 px-6 text-center">Status</th>
                    <th className="py-3 px-6 text-center">Actions</th>
                  </tr>
                </thead>
                <tbody className="text-gray-600 text-sm font-light">
                  {orders.map((order) => (
                    <tr className="border-b border-gray-200 hover:bg-gray-100">
                      <td className="py-3 px-6 text-left whitespace-nowrap">
                        <div className="flex items-center">
                          <div className="mr-2"></div>
                          <span className="font-medium">{order.id}</span>
                        </div>
                      </td>
                      <td className="py-3 px-6 text-left">
                        {order.items.map((item) => (
                          <div className="mb-3 flex items-center">
                            <div className="mr-2">
                              <img
                                className="w-6 h-6 rounded-full"
                                src={item.product.thumbnail}
                                alt={item.product.title}
                              />
                            </div>
                            <span className="w-full">
                              {item.product.title} - #{item.quantity} - $
                              {discountedPrice(item.product)}
                            </span>
                          </div>
                        ))}
                      </td>
                      <td className="py-3 px-6 text-center">
                        <div className="flex items-center justify-center">
                          ${order.totalAmount}
                        </div>
                      </td>
                      <td className="py-3 px-6 text-center">
                        <div>
                          <div>
                            <strong>{order.selectedAddress.name}</strong>,
                          </div>
                          <div>{order.selectedAddress.street},</div>
                          <div>{order.selectedAddress.city},</div>
                          <div>{order.selectedAddress.state},</div>
                          <div>{order.selectedAddress.pinCode},</div>
                          <div>{order.selectedAddress.phone}</div>
                        </div>
                      </td>
                      <td className="py-3 px-6 text-center">
                        {order.id !== editableOrderId ? (
                          <span
                            className={`${chooseColor(
                              order.status
                            )} py-1 px-3 rounded-full text-xs`}
                          >
                            {order.status}
                          </span>
                        ) : (
                          <select
                            value={order.status}
                            onChange={(e) => handleUpdate(e, order)}
                          >
                            <option value="pending">Pending</option>
                            <option value="dispatched">Dispatched</option>
                            <option value="delivered">Delivered</option>
                            <option value="cancelled">Cancelled</option>
                          </select>
                        )}
                      </td>
                      <td className="py-3 px-6 text-center">
                        <div className="flex item-center justify-center">
                          <div className="w-4 mr-4 transform hover:text-purple-500 hover:scale-110">
                            <EyeIcon
                              className="w-70 h-7"
                              onClick={(e) => handleShow(order)}
                            ></EyeIcon>
                          </div>
                          <div className="w-4 mr-2 transform hover:text-purple-500 hover:scale-110">
                            <PencilIcon
                              className="w-7 h-7"
                              onClick={(e) => handleEdit(order)}
                            ></PencilIcon>
                          </div>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
        <Pagination
          page={page}
          setPage={setPage}
          handlePage={handlePage}
          totalItems={totalOrders}
        ></Pagination>
      </div>
    </>
  );
};

export default AdminOrders;
